import React, { useState } from "react";

function Filter(props) {
  const { filterCheck = Function.prototype } = props;

  const [type, setType] = useState("All");

  const handleChoose = (event) => {
    filterCheck((props) => event.target.value);
    setType(event.target.value);
  };

  return (
    <form>
      <label>
        <input
          name="categories"
          type="radio"
          value="All"
          onChange={handleChoose}
          checked={type === "All"}
        />
        <span>All</span>
      </label>
      <label>
        <input
          name="categories"
          type="radio"
          value="Movies"
          onChange={handleChoose}
          checked={type === "Movies"}
        />
        <span>Movies</span>
      </label>
      <label>
        <input
          name="categories"
          type="radio"
          value="Series"
          onChange={handleChoose}
          checked={type === "Series"}
        />
        <span>Series</span>
      </label>
    </form>
  );
}

export { Filter };
