import React, { useState, useEffect } from "react";
import { Movies } from "../components/Movies";
import { Preloader } from "../components/Preloader";
import { Search } from "../components/Search";
import { Filter } from "../components/Filter";

const API_KEY = process.env.REACT_APP_API_KEY;

function Main() {
  const [movies, setMovies] = useState([]);
  const [filter, setfilters] = useState("");
  const [loading, setloading] = useState(true);

  const filterCheck = (filter) => {
    setfilters(filter);
  };

  const movieSearch = (str) => {
    setloading(true);
    if (filter === "All") {
      if (str === "") {
        return;
      } else {
        fetch(`https://www.omdbapi.com/?i=tt3896198&apikey=${API_KEY}&s=${str}`)
          .then((response) => response.json())
          .then((data) => {
            setMovies(data.Search);
            setloading(false);
          })
          .catch((err) => {
            console.log(err);
            setloading(false);
          });
      }
    } else if (filter === "Movies") {
      if (str === "") {
        return;
      } else {
        fetch(
          `https://www.omdbapi.com/?i=tt3896198&apikey=${API_KEY}&type=movie&s=${str}`
        )
          .then((response) => response.json())
          .then((data) => {
            setMovies(data.Search);
            setloading(false);
          })
          .catch((err) => {
            console.log(err);
            setloading(false);
          });
      }
    } else {
      if (str === "") {
        return;
      } else {
        fetch(
          `https://www.omdbapi.com/?i=tt3896198&apikey=${API_KEY}&type=series&s=${str}`
        )
          .then((response) => response.json())
          .then((data) => {
            setMovies(data.Search);
            setloading(false);
          })
          .catch((err) => {
            console.log(err);
            setloading(false);
          });
      }
    }
  };

  useEffect(() => {
    fetch(`https://www.omdbapi.com/?i=tt3896198&apikey=${API_KEY}&s=matrix`)
      .then((response) => response.json())
      .then((data) => {
        setMovies(data.Search);
        setloading(false);
      })
      .catch((err) => {
        console.log(err);
        setloading(false);
      });
  }, []);

  return (
    <main className="container content">
      <Search movieSearch={movieSearch} />
      <Filter filterCheck={filterCheck} />
      {loading ? <Preloader /> : <Movies movies={movies} />}
    </main>
  );
}
export { Main };
